<script>
import Carousel from "./VantImageView";
import ChatAdd from "@/components/ChatAddView";
import Kimage from "../assets/kakao-pay-black.c698e475.png"
import image1 from "../assets/gallery/HYW09498.jpg"
import image2 from "../assets/gallery/HYW09305.jpg"
import image3 from "../assets/gallery/HYW09384.jpg"
import image4 from "../assets/gallery/HYW09470.jpg"
import image5 from "../assets/gallery/HYW09542.jpg"
import image6 from "../assets/gallery/HYW09536.jpg"

export default {
  name: "Main",
  components: {
    Carousel,
    ChatAdd,
    'Map': () => import('@/components/Map'),
  },
  data() {
    return {
      calling: false,
      Dday: '',
      nowLink: '',
      copyData: [],
      btnClick: false,
      btnClick1: false,
      curPos: 0,
      postion: 0,
      start_x: 0,
      end_x: 0,
      IMAGE_WIDTH: 0,
      images: null,
      KakaoImage: [
        Kimage
      ],
      imageUrls: [
        image3,
        image1,
        image2,
        image4,
        image5,
        image6
      ],
    };
  }, created() {
    this.diffDay()
  },
  mounted() {
    this.nowLink = document.location.href;
    this.copyData.push(this.$refs.getData.innerHTML);
    this.copyData.push(this.$refs.getData1.innerHTML);
  },
  methods: {
    Call() {
      this.calling === !true ? this.calling = true : this.calling = false
    },
    diffDay() {
      var Dday = new Date(2022, 10, 27);    // D-day(2017년 8월 30일)를 셋팅한다.
      var now = new Date();                    // 현재(오늘) 날짜를 받아온다.
      var gap = now.getTime() - Dday.getTime();    // 현재 날짜에서 D-day의 차이를 구한다.
      var result = Math.floor(gap / (1000 * 60 * 60 * 24)) * -1;    // gap을 일(밀리초 * 초 * 분 * 시간)로 나눈다. 이 때 -1 을 곱해야 날짜차이가 맞게 나온다.
      this.Dday = result;
    },
    CopyLink() {
      this.$copyText(this.nowLink).then(function (e) {
        alert("페이지 주소가 복사되었습니다.\n" + e.text);
      });
    },
    Copy() {
      console.log()
      this.$copyText(this.copyData[0]).then(function (e) {
        alert("계좌번호가 복사되었습니다.\n" + e.text);
      });
    }, Copy1() {
      console.log()
      this.$copyText(this.copyData[1]).then(function (e) {
        alert("계좌번호가 복사되었습니다.\n" + e.text);
      });
    },
    kakaoLink() {
      window.Kakao.Link.sendDefault({
            objectType: 'feed',
            content: {
              title: '김재욱 & 김은희 결혼합니다.',
              description: '2022년 11월 27일 오전 10시 30분 \n노비아갈라 전자관 5층 보타닉가든홀',
              imageUrl:
                  'http://drive.google.com/uc?export=view&id=1-CweZ1mIoQjjSLFbgdU0ElvKp8sfz3ns',
              link: {
                mobileWebUrl: 'https://ukkidoki.github.io/wedding/',
                webUrl: 'https://ukkidoki.github.io/wedding/',
              },
            },
            buttons: [
              {
                title: '청첩장 보기',
                link: {
                  mobileWebUrl: 'https://ukkidoki.github.io/wedding/',
                  webUrl: 'https://ukkidoki.github.io/wedding/',
                },
              }
            ]
          }
      )
    },
    showPay() {
      this.btnClick != true ? this.btnClick = true : this.btnClick = false;
    },
    showPay1() {
      this.btnClick1 != true ? this.btnClick1 = true : this.btnClick1 = false;
    }
  }
}
